// Connectivity plugin
import { getConnectionState } from './selectors'
import { WS_CONNECT_FINISHED } from './actionTypes'

// Other plugins
import { getPlatform } from '../../auth/interface/selectors'
import { delay } from '../../common/utils'
import { UNSUBSCRIBE_FINISHED } from '../../subscription/interface/actionTypes'

/**
 * Util for waiting for the websocket / subscription to reconnect.
 * Assumption is that the websocket is in the middle of reconnect attempts. This
 *    is why the timeout is so long; one of the two scenarios should be guaranteed
 *    to happen before then.
 * @param {Object} container The bottle container.
 * @param {number} timeout The time, in milliseconds, to wait before timing out.
 * @return {boolean} Whether the websocket has reconnected or not.
 */
export async function waitForReconnect (container, timeout = 60000) {
  const { context, Notifications } = container
  const platform = getPlatform(context.getState())
  const { connected: isConnected } = getConnectionState(context.getState(), platform)

  if (isConnected) {
    return true
  }

  const result = await Promise.race([
    Notifications.takeAction(WS_CONNECT_FINISHED),
    Notifications.takeAction(UNSUBSCRIBE_FINISHED),
    delay(timeout)
  ])

  if (result && result.actionType && result.actionType === WS_CONNECT_FINISHED) {
    return true
  }

  return false
}
