import replaceTrackOperation, { validate } from './replaceTrack'
import replaceTrackWebrtcOperation from './webrtcReplaceTrack'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation (bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.replaceTrack', () => {
    const operation = replaceTrackOperation(bottle.container)
    // Add meta-data to the operation for the CallManager.
    operation.isNegotiation = false
    operation.type = OPERATIONS.REPLACE_TRACK
    operation.validate = validate
    return operation
  })

  bottle.factory('Callstack.utils.webRtcReplaceTrack', () => replaceTrackWebrtcOperation(bottle.container))
}
