// The interface to follow.
import { createAPI, reducer, name } from '../interface'

// Other plugins.
import { update as updateConfig } from '../../config/interface/actions'
import { mergeValues } from '../../common/utils'
import * as connectivityActionTypes from '../../connectivity/interface/actionTypes'
import { NOTIFICATION_RECEIVED } from '../../notifications/interface/actionTypes'

// Operations
import * as initOperations from './operations'

// Intervals
import * as initIntervals from './intervals'

// Requests
import * as initRequests from './requests'

// Libraries
import { pick } from 'lodash/fp'

// Parse and/or Validate
import { enums, validation as v8n, parse } from '../../common/validation'

/**
 * Auth plugin factory for anonymous call scenarios.
 * @method anonymousAuth
 * @param  {Object} options Configuration options for authentication. See above.
 * @return {Object} An anonymous authentication plugin.
 */
export default function authFactory (options = {}, bottle) {
  /**
   * Configuration options for the anonymous Authentication feature.
   * @public
   * @name config.authentication
   * @memberof config
   * @instance
   * @param {Object}  authentication Authentication configs.
   * @param {Object}  authentication.subscription
   * @param {number} [authentication.subscription.serviceUnavailableMaxRetries=3] The maximum number of times this client will retry in order to subscribe for a
   * given service, while getting 'Service Unavailable' from backend.
   * @param {string} [authentication.subscription.protocol='https'] Protocol to be used for subscription requests.
   * @param {string}  authentication.subscription.server Server to be used for subscription requests.
   * @param {Number} [authentication.subscription.port=443] Port to be used for subscription requests.
   * @param {Array}  [authentication.subscription.service] Services to subscribe to for notifications.
   * @param {Object}  authentication.websocket
   * @param {string} [authentication.websocket.protocol='wss'] Protocol to be used for websocket notifications.
   * @param {string}  authentication.websocket.server Server to be used for websocket notifications.
   * @param {Number} [authentication.websocket.port=443] Port to be used for websocket notifications.
   */

  const defaultOptions = {
    subscription: {
      expires: 3600,
      serviceUnavailableMaxRetries: 3,
      protocol: 'https',
      server: null,
      port: 443,
      version: '1', // not documented, but important
      service: []
    },
    websocket: {
      protocol: 'wss',
      server: null,
      port: 443
    }
  }

  // config validation
  const v8nValidation = v8n.schema({
    subscription: v8n.schema({
      serviceUnavailableMaxRetries: v8n.positive(),
      protocol: enums(['http', 'https']),
      server: v8n.string(),
      port: v8n.positive(),
      version: enums(['1']), // not documented, but important
      service: v8n.array().every.string()
    }),
    websocket: v8n.schema({
      protocol: v8n.string(),
      server: v8n.string(),
      port: v8n.positive()
    })
  })
  const parseOptions = parse('authentication', v8nValidation)

  options = mergeValues(defaultOptions, options)
  parseOptions(options)

  function initPlugin (container) {
    const { context } = container
    // Send the provided options to the store.
    // This will be `state.config[name]`.
    context.dispatch(updateConfig(options, name))

    // Take connectivity.WS_RECONNECT_FAILED actions
    function receiveConnectionLostPattern (action) {
      return action.type === connectivityActionTypes.WS_RECONNECT_FAILED
    }

    container.Notifications.takeEveryAction(receiveConnectionLostPattern, action => {
      const { AuthenticationOperations: operations } = container
      operations.onConnectionLost()
    })

    // Take subscription gone notifications
    function receiveGoneNotificationPattern (action) {
      return (
        action.type === NOTIFICATION_RECEIVED &&
        action.payload.notificationMessage &&
        action.payload.notificationMessage.eventType === 'gone'
      )
    }

    container.Notifications.takeEveryAction(receiveGoneNotificationPattern, action => {
      const { AuthenticationOperations: operations } = container
      operations.onSubscriptionGone()
    })
  }

  // Register the component factory functions to the bottle.
  bottle.factory('AuthenticationAPI', container => {
    const fullAPI = createAPI(container)

    // Auth APIs that only return state.
    const apiGetters = ['getUserInfo', 'getConnection', 'getServices']
    return pick(apiGetters, fullAPI)
  })

  // Initialize operations
  for (const name in initOperations) {
    initOperations[name](bottle)
  }

  // Initialize intervals
  for (const name in initIntervals) {
    initIntervals[name](bottle)
  }

  // Initialize requests
  for (const name in initRequests) {
    initRequests[name](bottle)
  }

  bottle.defer(initPlugin)

  return {
    name,
    reducer
  }
}
