// Auth plugin.
import * as actions from '../../interface/actions'
import * as eventTypes from '../../interface/eventTypes'
import { getAuthConfig, getServiceUnavailableMaxRetries } from '../../interface/selectors'

// Other plugins.
import { wsAttemptConnect } from '../../../connectivity/interface/actions'
import { WS_CONNECT_FINISHED } from '../../../connectivity/interface/actionTypes'
import BasicError, { authCodes } from '../../../errors'

// Constants
import { platforms } from '../../../constants'

export default function createOperation (container) {
  const { logManager, context, emitEvent, Notifications, AuthenticationRequests, AuthenticationIntervals } = container
  const log = logManager.getLogger('AUTH')

  /**
   * Handles anonymous subscription and establishing the websocket.
   * Mocks the connection process using the auth actions to trigger required
   *      side-effects elsewhere.
   * @method anonymousConnect
   * @param  {Object} credentials Credentials used to connect.
   * @return {Object} Response or error object.
   */
  async function anonymousConnect (credentials) {
    const delay = ms => new Promise(resolve => setTimeout(resolve, ms))

    // Retrieve the connection info.
    const config = getAuthConfig(context.getState())

    const requestOptions = {
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json'
      }
    }

    if (credentials.realm) {
      requestOptions.queryParams = {
        tokenrealm: credentials.realm
      }
    }

    const subscriptionInfo = {
      ...config.subscription,
      isAnonymous: true,
      // TODO: Hardcode / override this here?
      service: ['callMe']
    }

    let response
    let attemptNum = 1 // keeps count of how many subscription requests we sent so far
    const maxReAttempts = getServiceUnavailableMaxRetries(context.getState())

    // 1 + : there will be one initial attempt followed by up to a maximum number of re-attempts.
    while (attemptNum <= 1 + maxReAttempts) {
      log.info('Subscribing to callMe service.')
      try {
        response = await AuthenticationRequests.subscribe(subscriptionInfo, credentials, requestOptions)

        if (!response.subscriptionParams.service.includes('callMe')) {
          // Subscription was successful, but didn't include the callMe service.
          log.info('Subscription failed. Call Me service not provided with subscription.')

          const error = new BasicError({
            message: 'Subscription failed to receive required service: callMe',
            code: authCodes.MISSING_SERVICE
          })

          context.dispatch(
            actions.connectFinished({
              error
            })
          )

          emitEvent(eventTypes.AUTH_CHANGE, { error })

          return
        } else {
          // We got a succesul response to our latest `subscribe` request,
          // so cancel any further `subscribe` attempts.
          break
        }
      } catch (error) {
        log.info('Subscription failed. Error: ', error)
        if (error.code === authCodes.LINK_SUBSCRIBE_UNAVAILABLE && error.retryAfter) {
          if (attemptNum === 1 + maxReAttempts) {
            // Trigger the usual action in case of failure & abort right away.
            // NOTE: This action won't mention the fact that we tried so many times,
            // as it should be transparent to the application level.
            context.dispatch(actions.connectFinished({ error }, platforms.LINK))

            emitEvent(eventTypes.AUTH_CHANGE, { error })

            // Just the log would include this info, to help in debugging.
            log.debug(`Subscription failed: ${error} after ${attemptNum - 1} re-attempts.`)
            return
          }

          // Wait for the subscribe delay
          await delay(error.retryAfter)
          log.debug(`Retrying after a ${error.retryAfter} ms delay...`)

          // Attempt another `subscribe` request by
          // incrementing our attempts count.
          // We got a 503 error, but we're still allowed to retry our `subscribe` request.
          // We also won't log any of these intermediary 503 errors, since we'll make yet another attempt.
          attemptNum++
        } else {
          // We got an error other than 503 (or the 503 did not have a retryAfter value).
          // Either way, we treat it as a final error as it should mark the end of our attempts.
          context.dispatch(actions.connectFinished({ error }, platforms.LINK))

          emitEvent(eventTypes.AUTH_CHANGE, { error })

          if (attemptNum > 1) {
            log.debug(`Subscription subsequently failed: ${error} (after getting an initial 503 response).`)
          } else {
            // We got an error the first time we tried to subscribe.
            log.debug(`Subscription failed: ${error.message}`)
          }
          return
        }
      }
    }

    log.info('Successfully subscribed to callMe service. Connecting to websocket.')

    // Connect the websocket.
    const websocketInfo = {
      ...config.websocket,
      url: response.subscriptionParams.notificationChannel
    }

    // Request the websocket connection.
    context.dispatch(wsAttemptConnect(websocketInfo, platforms.LINK))

    // Wait for the action that signifies the result of the above action.
    try {
      await Notifications.takeAction(action => {
        return action.type === WS_CONNECT_FINISHED && action.meta.platform === platforms.LINK
      })
    } catch (error) {
      log.info('Failed to connect to websocket. Error: ', error)
      // Websocket failed to connect.
      context.dispatch(
        actions.connectFinished(
          {
            error
          },
          platforms.LINK
        )
      )

      emitEvent(eventTypes.AUTH_CHANGE, { error })

      return
    }

    log.info('Successfully connected to websocket.')

    const connection = {
      server: {
        ...config.subscription
      },
      username: credentials.username,
      requestOptions,
      // Store the fact that this is an anonymous subscription as part of
      //      connection state.
      isAnonymous: true,
      realm: credentials.realm
    }

    context.dispatch(
      actions.connectFinished(
        {
          userInfo: {
            username: credentials.anonUsername
          },
          subscription: {
            ...response.subscriptionParams,
            url: response.subscription
          },
          connection
        },
        platforms.LINK
      )
    )

    // Start the resubscription interval
    AuthenticationIntervals.resubInterval.startResubInterval(connection)

    // Emit an auth changed event
    emitEvent(eventTypes.AUTH_CHANGE, {})
  }

  return anonymousConnect
}
