// Call plugin.
import pluginInterface from '../interface'
import createAnonApi from '../interface/api/anonymous'

import createMiddleware from '../link/middleware'
import createRequests from '../link/requests'

import * as linkOperations from '../link/operations'
import * as anonOperations from './operations'

import parseConfigs, { mergeDefaults } from '../configs'

import setupCallstack from '../callstack'

// Other plugins.
import { update as updateConfig, setSdpHandlers } from '../../config/interface/actions'

// Libraries.
import { getWebRTCSupportCapabilities } from 'kandy-webrtc'

/**
 * Anonymous Call plugin factory.
 */
export default function anonCallFactory (options = {}, bottle) {
  /*
   * Init function to setup the Call plugin after the all plugins have been
   *    created and redux set-up.
   * Handles configuration provided by the application and puts it into state.
   */
  function initPlugin (container) {
    const { context, logManager } = container
    const log = logManager.getLogger('CALL')

    const { mediaDevices, peerConnection } = getWebRTCSupportCapabilities()
    if (!mediaDevices || !peerConnection) {
      log.warn(
        'Calls are not supported on this platform due to lack of WebRTC support. Call APIs will not be available.'
      )
    }

    // Parse the options provided by the application and make any adjustments needed
    //    (for backwards-compatibility reasons).
    options = parseConfigs(options)
    // Then merge the defaults into them and validate the values.
    options = mergeDefaults(options)

    // Change sdpSemantics to always be for Unified-Plan
    if (options.defaultPeerConfig.sdpSemantics !== 'unified-plan') {
      log.warn('Only `unified-plan` is supported as sdpSemantics. Switching to `unified-plan`.')
      options.defaultPeerConfig.sdpSemantics = 'unified-plan'
    }

    context.dispatch(updateConfig(options, pluginInterface.name))

    // `call.removeH264Codecs` was removed in v6.0, in faviour of createCodecRemover.
    if (typeof options.removeH264Codecs !== 'undefined') {
      log.warn(
        'The `call.removeH264Codecs` configuration has been removed. Please see the documentation for the `sdpHandlers` configuration for how to implement similar behaviour.'
      )
    }

    // Update the SDP handlers in the config (user provided + some of our own)
    context.dispatch(setSdpHandlers(options.sdpHandlers))
  }

  // Initialize the Callstack in the bottle's scope.
  setupCallstack(bottle)
  // Initialize the Link+Anon-specific operations in the bottle's scope.
  for (const name in linkOperations) {
    linkOperations[name](bottle)
  }
  for (const name in anonOperations) {
    anonOperations[name](bottle)
  }

  // Register the component factory functions to the bottle.
  bottle.factory('CallAPI', createAnonApi)
  bottle.factory('CallRequests', createRequests)

  bottle.defer(initPlugin)

  return {
    name: pluginInterface.name,
    reducer: pluginInterface.reducer,
    middleware: createMiddleware,
    capabilities: ['callMe']
  }
}
