// Operations.
import addMediaOperation, { validate as addMediaValidate } from './addMedia'
import webrtcAddMediaOperation from './webrtcAddMedia'
import rollbackOperation from './rollbackAddMedia'
import addBasicMediaOperation, { validate as basicValidate } from './addBasicMedia'

import remoteOffer from './remoteOffer'
import remoteAnswer from './remoteAnswer'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation (bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.addMedia', () => {
    const operation = addMediaOperation(bottle.container)
    operation.remoteOffer = remoteOffer(bottle.container)
    operation.remoteAnswer = remoteAnswer(bottle.container)
    // Add meta-data to the operation for the CallManager.
    operation.isNegotiation = true
    operation.type = OPERATIONS.ADD_MEDIA
    operation.validate = addMediaValidate
    return operation
  })
  bottle.factory('Callstack.operations.addBasicMedia', () => {
    const operation = addBasicMediaOperation(bottle.container)
    operation.remoteAnswer = remoteAnswer(bottle.container)
    // Add meta-data to the operation for the CallManager.
    operation.isNegotiation = true
    operation.type = OPERATIONS.ADD_BASIC_MEDIA
    operation.validate = basicValidate
    return operation
  })

  // Helpers used only within the operation.
  bottle.factory('Callstack.utils.webrtcAddMedia', () => webrtcAddMediaOperation(bottle.container))
  bottle.factory('Callstack.utils.rollbackAddMedia', () => rollbackOperation(bottle.container))
}
