import resyncCallStateOperation, { validate } from './resyncCallState'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation (bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.resyncCallState', () => {
    const operation = resyncCallStateOperation(bottle.container)
    // Add meta-data to the operation for the CallManager.
    operation.isNegotiation = false
    operation.type = OPERATIONS.RESYNC
    operation.validate = validate
    return operation
  })
}
