import localOperation, { validate } from './end'
import remoteOperation from './callStatusUpdateEnded'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation (bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.end', () => {
    const operation = localOperation(bottle.container)
    // Add meta-data to the operation for the CallManager.
    operation.isNegotiation = false
    operation.type = OPERATIONS.END
    operation.validate = validate
    return operation
  })
  bottle.factory('Callstack.notifications.callStatusUpdateEnded', () => remoteOperation(bottle.container))
}
