// Call plugin
import { RESYNC } from '../../interface/actionTypes'

export default function createMiddleware (context) {
  const { CallManager } = context.container

  async function resyncSessionStatuses (action) {
    if (!shouldHandlePattern(action)) {
      // Don't handle it.
      return
    }

    // Handle it.
    const callId = action.payload.id
    await CallManager.resyncCallState(callId)
  }

  return resyncSessionStatuses
}

// The `incomingCallPattern` function.
export function shouldHandlePattern (action) {
  return action.type === RESYNC
}
