// Library.
import sdpTransform from 'sdp-transform'
import { compose, join, map, trimCharsStart, trimEnd } from 'lodash/fp'

// Refers to functions in src/callstack/sdp/pipeline.js
export const sdpToYaml = info => originalSdp => newSdp => results => finalDiffObject =>
  `
---
sdp-pipeline-run:
  info:
    callId: ${info.callId}
    type: ${info.type}
    step: ${info.step}
    endpoint: ${info.endpoint}
    bandwidth: ${displayBandwidth(info.bandwidth)}
  sdp: |
    ${displaySdp(fourSpaces)(originalSdp)}
  handlers:
    ${displayHandlers(results)}
  final:
    diff: |
      ${displayDiffs(sixSpaces)(finalDiffObject.text)}
    sdp: |
      ${finalDiffObject.changed ? displaySdp(sixSpaces)(newSdp) : 'No changes to the SDP'}
...
`

// If the function is anonymous give the function body as the 'name'
export const formattedFunctionName = handler =>
  handler.name ||
  handler
    .toString()
    .slice(0, 120)
    .replace(/\n/g, '')

// Display helper functions
const displayBandwidth = bandwidth =>
  bandwidth &&
  JSON.stringify(bandwidth, null, ' ')
    .replace(/\n/g, ' ')
    .replace(/ {2}/g, ' ')

const displaySdp = indentation => sdp =>
  sdpTransform
    .write(sdp)
    .replace(/\n/g, `\n${indentation}`)
    .trimEnd()

const handlerToYaml = ({ name, diff }) =>
  // replacing newlines in case the name is the function.
  `${fourSpaces}- name: ${name.replace(/\n/g, '')}
      diff: |
        ${displayDiffs(eightSpaces)(diff)}
`

const displayHandlers = compose([trimEnd, trimCharsStart(' \n'), join(''), map(handlerToYaml)])

const displayDiffs = indentation => diffed => (diffed ? diffed.replace(/\n/g, `\n${indentation}`) : 'No changes found')

// Space constants
const fourSpaces = '    '
const sixSpaces = '      '
const eightSpaces = '        '
