import registerPushDeviceToken from './registerPushDeviceToken'
import unregisterPushDeviceToken from './unregisterPushDeviceToken'

/*
 * Register the operation with the bottle. This will make it available on the
 *    top-level container under its namespace.
 */
export default function registerOperations (bottle) {
  bottle.factory('NotificationsOperations.registerPushDeviceToken', () => {
    return registerPushDeviceToken(bottle.container)
  })
  bottle.factory('NotificationsOperations.unregisterPushDeviceToken', () => {
    return unregisterPushDeviceToken(bottle.container)
  })
}
