// Call plugin.
import { callActions } from '../../../interface/actions'
import { getCallByWrtcsSessionId } from '../../../interface/selectors'
import { OPERATIONS, OP_TRANSITIONS } from '../../../interface/constants'
import { CALL_STATES } from '../../../constants'
import { CALL_OPERATION, CALL_STATE_CHANGE, CALL_TRACKS_REMOVED } from '../../../interface/eventTypes'

import { generateEndParams } from '../../utils/call'

/**
 * Bottle wrapper for "call status ended" notification handler.
 * @return {Function}
 */
export default function callStatusEndedOperation (container) {
  const { context, emitEvent, CallstackWebrtc, logManager } = container

  /**
   * Handle a "call ended" notification from the backend.
   *
   * This notification means that the session has ended on the other side and we need to end our session as well.
   * Handling the notification will cleanup the session's media and puts the call in ENDED state.
   * Responsibilities:
   *    1. Have the callstack perform the required webRTC cleanup process.
   *    2. Update call state to ENDED (via redux action).
   *    3. Emit events to the application about the operation.
   * @method callStatusUpdateEnded
   * @param {string}  wrtcsSessionId         ID that the server uses to identify the session.
   * @param {Object}  params
   * @param {string}  [params.reasonText]    Human-readable explanation for the call change.
   * @param {string}  [params.statusCode]    Code representing the reason for the call change.
   * @param {string}  [params.remoteName]    Name of the remote participant.
   * @param {string}  [params.remoteNumber]  Number of the remote participant.
   */
  async function callStatusUpdateEnded (wrtcsSessionId, { reasonText, statusCode, remoteName, remoteNumber } = {}) {
    const currentCall = getCallByWrtcsSessionId(context.getState(), wrtcsSessionId)

    const log = logManager.getLogger('CALL', (currentCall || {}).id)
    log.info('Received call status ended notice; handling.', { wrtcsSessionId })

    if (!currentCall) {
      log.info('Call ended notice for unknown wrtcsSession. Ignoring.', { wrtcsSessionId })
      return
    }

    if (currentCall.state === CALL_STATES.ENDED) {
      log.info('Call ended notice for already Ended call. Ignoring.', { wrtcsSessionId })
      return
    }

    const prevCall = currentCall

    log.debug(`Call ended notice caused by ${reasonText} (Status Code: ${statusCode}).`)

    await CallstackWebrtc.closeCall(currentCall.webrtcSessionId)

    log.info(`Finished handling call ended notice. Changing to ${CALL_STATES.ENDED}.`)

    context.dispatch(
      callActions.endCallFinish(
        currentCall.id,
        generateEndParams(currentCall.state, false, {
          reasonText,
          statusCode,
          remoteName,
          remoteNumber
        })
      )
    )

    emitEvent(CALL_OPERATION, {
      callId: currentCall.id,
      operation: OPERATIONS.END,
      transition: OP_TRANSITIONS.FINISH,
      isLocal: false,
      previous: prevCall.remoteOp
    })

    // Tell the application that no tracks are available any longer.
    emitEvent(CALL_TRACKS_REMOVED, {
      callId: currentCall.id,
      trackIds: [...prevCall.localTracks, ...prevCall.remoteTracks]
    })

    emitEvent(CALL_STATE_CHANGE, {
      callId: currentCall.id,
      transition: { reasonText, statusCode },
      previous: {
        state: prevCall.state,
        localHold: prevCall.localHold,
        remoteHold: prevCall.remoteHold
      }
    })
  }

  return callStatusUpdateEnded
}
