// Link-specific middlewares.
import callCancelledNotification from './callCancelledNotification'
import callStatusUpdateEndedNotification from './callStatusUpdateEndedNotification'
import callStatusUpdateFailedNotification from './callStatusUpdateFailedNotification'
import callStatusUpdateRingingNotification from './callStatusUpdateRingingNotification'
import sessionStatusUpdateEndedNotification from './sessionStatusUpdateEndedNotification'
import incomingCallNotification from './incomingCallNotification'
import remoteAnswerNotification from './remoteAnswerNotification'
import remoteOfferNotification from './remoteOfferNotification'
import resyncSessionStatuses from './resyncSessionStatuses'
import sessionProgressNotification from './sessionProgressNotification'
import setTurnCredentials from './setTurnCredentials'

// Callstack middlewares.
import callStackMiddlewares from '../../callstack/middleware'

const factories = [
  callCancelledNotification,
  callStatusUpdateEndedNotification,
  callStatusUpdateFailedNotification,
  callStatusUpdateRingingNotification,
  sessionStatusUpdateEndedNotification,
  incomingCallNotification,
  remoteAnswerNotification,
  remoteOfferNotification,
  resyncSessionStatuses,
  sessionProgressNotification,
  setTurnCredentials,
  ...callStackMiddlewares
]

/**
 * Link Call middleware factory function.
 * @method createMiddleware
 * @param  {Object} context The factory context.
 * @return {Function} A redux middleware.
 */
export default function createMiddleware (context) {
  // Create the middlewares scoped with the `context` object.
  const middlewares = factories.map(factory => factory(context))

  /*
   * Redux middleware: Watch for specific actions and trigger operations
   *    when we see them.
   */
  return next => action => {
    // TODO: Do we care about return values? ...no?
    middlewares.forEach(middleware => middleware(action))
    return next(action)
  }
}
