import {
  SESSION_ICE_GATHERING_STATE_CHANGE,
  SESSION_ICE_CANDIDATE_COLLECTED,
  SESSION_ICE_COLLECTION_SCHEDULED_CHECK
} from '../../../webrtc/interface/actionTypes'

/**
 * Bottle wrapper for "ICE collection check" triggers.
 * @return {Function}
 */
export default function callCollectionCheckMiddleware (context) {
  const operations = context.container.Callstack.operations

  async function callCollectionCheck (action) {
    if (!shouldHandlePattern(action)) {
      // Don't handle it.
      return
    }

    // Handle it.
    const { id, ...iceCollectionInfo } = action.payload
    await operations.callIceCollectionCheck(id, iceCollectionInfo)
  }

  return callCollectionCheck
}

export function shouldHandlePattern (action) {
  return (
    action.type === SESSION_ICE_GATHERING_STATE_CHANGE ||
    action.type === SESSION_ICE_CANDIDATE_COLLECTED ||
    action.type === SESSION_ICE_COLLECTION_SCHEDULED_CHECK
  )
}
