// Other plugins.
import * as authActions from '../../../../auth/interface/actions'

export default function createAnonOperations (container) {
  const { context, AuthenticationOperations } = container

  /**
   * When an anonymous call ends, the user should be unsubscribed since they
   *    were only authenticated for the call.
   * @method anonymousCallEnd
   */
  function anonymousCallEnd () {
    // When the call ends, we want to disconnect the user.
    context.dispatch(authActions.disconnect())

    AuthenticationOperations.anonymousDisconnect()
  }

  return anonymousCallEnd
}
