import * as callOperations from './operations'
import * as callNotifications from './notifications'

import initSdpOperations from './sdp'
import initWebrtcOperations from './webrtc'
import initCallManager from './callManager'
import initCallReporter from './reporter'

/**
 * Callstack  factory function.
 * @method createCallstack
 * @param  {Object} bottle The bottle.
 */
export default function createCallstack (bottle) {
  // Initialize the operation handlers, adding them to the bottle.
  // They will be at `Callstack.operations` and `Callstack.utils`.
  for (const name in callOperations) {
    callOperations[name](bottle)
  }
  // Initialize the notification handlers, adding them to the bottle.
  // They will be at `Callstack.notifications`.
  for (const name in callNotifications) {
    callNotifications[name](bottle)
  }

  initCallReporter(bottle)
  // Initialize the SDP and WebRTC operations.
  initSdpOperations(bottle)
  initWebrtcOperations(bottle)

  initCallManager(bottle)
}
