import anonymousConnect from './anonymousConnect'
import anonymousDisconnect from './anonymousDisconnect'
import onConnectionLost from './onConnectionLost'
import onSubscriptionGone from './onSubscriptionGone'

/*
 * Register the operation with the bottle. This will make it available on the
 *    top-level container under its namespace.
 */
export default function registerOperations (bottle) {
  bottle.factory('AuthenticationOperations.anonymousConnect', () => {
    return anonymousConnect(bottle.container)
  })
  bottle.factory('AuthenticationOperations.anonymousDisconnect', () => {
    return anonymousDisconnect(bottle.container)
  })
  bottle.factory('AuthenticationOperations.onConnectionLost', () => {
    return onConnectionLost(bottle.container)
  })
  bottle.factory('AuthenticationOperations.onSubscriptionGone', () => {
    return onSubscriptionGone(bottle.container)
  })
}
