import processNotification from './processNotification'
import enableWebsocketChannel from './enableWebsocketChannel'

/*
 * Register the operation with the bottle. This will make it available on the
 *    top-level container under its namespace.
 */
export default function registerOperations (bottle) {
  bottle.factory('NotificationsOperations.enableWebsocketChannel', () => {
    return enableWebsocketChannel(bottle.container)
  })
  bottle.factory('NotificationsOperations.processNotification', () => {
    return processNotification(bottle.container)
  })
}
