/*
 * Webrtc Helper Operations available in the Callstack.
 * This file is where the names for the operations are defined.
 */
import closeCall from './establish/closeCall'

import createLocal from './media/createLocal'

import generateOffer from './negotiation/generateOffer'
import handleOffer from './negotiation/handleOffer'
import receivedAnswer from './negotiation/receivedAnswer'

export default function initOperation (bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('CallstackWebrtc.closeCall', () => closeCall(bottle.container))
  bottle.factory('CallstackWebrtc.createLocal', () => createLocal(bottle.container))
  bottle.factory('CallstackWebrtc.generateOffer', () => generateOffer(bottle.container))
  bottle.factory('CallstackWebrtc.handleOffer', () => handleOffer(bottle.container))
  bottle.factory('CallstackWebrtc.receivedAnswer', () => receivedAnswer(bottle.container))
}
