/**
 * The WebSocket to the server has changed state.
 *
 * This event is only emitted when the WebSocket is connected, or has lost connection.
 *
 * @public
 * @memberof connection
 * @event ws:change
 * @param {Object} params
 * @param {string} params.platform The platform
 */
export const WS_CHANGE = 'ws:change'
