  [OPERATIONS.ANSWER]: 'answer',
  SLOW_ANSWER: 'answerSlow',
  [OPERATIONS.REJECT]: 'reject',
  [OPERATIONS.IGNORE]: 'ignore',
  [OPERATIONS.FORWARD_CALL]: 'forward',
  [OPERATIONS.END]: 'end',
  [OPERATIONS.DIRECT_TRANSFER]: 'directTransfer',
  [OPERATIONS.CONSULTATIVE_TRANSFER]: 'consultativeTransfer',
  [OPERATIONS.JOIN]: 'join',
  [OPERATIONS.HOLD]: 'hold',
  [OPERATIONS.UNHOLD]: 'unhold',
  [OPERATIONS.ADD_MEDIA]: 'addMedia',
  [OPERATIONS.REMOVE_MEDIA]: 'removeMedia',
  [OPERATIONS.ADD_BASIC_MEDIA]: 'addBasicMedia',
  [OPERATIONS.REMOVE_BASIC_MEDIA]: 'removeBasicMedia',
  [OPERATIONS.REPLACE_TRACK]: 'replaceTrack',
  [OPERATIONS.MEDIA_RESTART]: 'iceRestart',
  [OPERATIONS.RESYNC]: 'resyncCallState',
  [OPERATIONS.PLAY_AUDIO]: 'playAudioFile',
  [OPERATIONS.SEND_RINGING_FEEDBACK]: 'sendRingingFeedback',
