/*
 * SDP Helper Operations available in the Callstack.
 * This file is where the names for the operations are defined.
 */
import compareMedia from './media/compareMedia'
import runPipeline from './pipeline'

export default function initOperation (bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('CallstackSDP.compareMedia', () => compareMedia(bottle.container))
  bottle.factory('CallstackSDP.runPipeline', () => runPipeline(bottle.container))
}
