// Operations.
import unholdOperation, { validate } from './unhold'
import rollbackOperation from './rollbackUnhold'

import createRemoteOffer from './remoteOffer'
import createRemoteAnswer from './remoteAnswer'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation (bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.unhold', () => {
    const operation = unholdOperation(bottle.container)
    operation.remoteOffer = createRemoteOffer(bottle.container)
    operation.remoteAnswer = createRemoteAnswer(bottle.container)
    // Add meta-data to the operation for the CallManager.
    operation.isNegotiation = true
    operation.type = OPERATIONS.UNHOLD
    operation.validate = validate
    return operation
  })

  bottle.factory('Callstack.utils.rollbackUnhold', () => rollbackOperation(bottle.container))
}
