// Call plugin.
import { callActions } from '../../../interface/actions'
import { OPERATIONS, OP_STATUS } from '../../../interface/constants'
import { COMPLEX_OPERATION_MESSAGES, STATUS_CODES } from '../../../constants'
import { getCallByWrtcsSessionId } from '../../../interface/selectors'
import * as eventTypes from '../../../interface/eventTypes'

// Other plugins.
import BasicError, { callCodes } from '../../../../errors'

/**
 * Bottle wrapper for "call status failed" notification handler.
 * @return {Function}
 */
export default function callStatusFailedOperation (container) {
  const { context, emitEvent, logManager } = container
  /**
   * A call status notification indicating that an operation has failed and needs to be handled.
   *
   * This operation handles the "session fail" notification by generating a transition object
   *    containing proper statusCode and reasonText and removing the current pending operation.
   * Responsibilities:
   *    1. Generate transition object containing proper statusCode and reasonText depending on pending operation.
   * @method callStatusUpdateFailed
   * @param {string}   wrtcsSessionId ID that the server uses to identify the session.
   * @param {Object}   params        Parameters describing the incoming call.
   */
  function callStatusUpdateFailed (wrtcsSessionId, params) {
    const { reasonText, statusCode } = params
    const currentCall = getCallByWrtcsSessionId(context.getState(), wrtcsSessionId)

    const log = logManager.getLogger('CALL', (currentCall || {}).id)
    log.info('Received call operation failed notice; handling.', { wrtcsSessionId })

    if (!currentCall) {
      log.info('Call operation failed notice for unknown wrtcsSession. Ignoring.', { wrtcsSessionId })
      return
    }

    const localOp = currentCall.localOp
    if (localOp && localOp.operation && localOp.status === OP_STATUS.PENDING) {
      let transition
      let finishAction
      switch (localOp.operation) {
        case OPERATIONS.DIRECT_TRANSFER:
          finishAction = callActions.directTransferFinish
          // TODO: May or may not need this when generic operation event is setup.
          transition = {
            reasonText: reasonText || COMPLEX_OPERATION_MESSAGES.DIRECT_TRANSFER_FAILURE,
            statusCode: statusCode || STATUS_CODES.DIRECT_TRANSFER_FAILURE
          }
          break
        case OPERATIONS.CONSULTATIVE_TRANSFER:
          finishAction = callActions.consultativeTransferFinish
          transition = {
            reasonText: reasonText || COMPLEX_OPERATION_MESSAGES.CONSULTATIVE_TRANSFER_FAILURE,
            statusCode: statusCode || STATUS_CODES.CONSULTATIVE_TRANSFER_FAILURE
          }
          break
        case OPERATIONS.JOIN:
          finishAction = callActions.joinFinish
          transition = {
            reasonText: reasonText || COMPLEX_OPERATION_MESSAGES.JOIN_FAILURE,
            statusCode: statusCode || STATUS_CODES.JOIN_FAILURE
          }
          break
      }
      log.debug(`Processing as a local ${localOp.operation} failure (${transition.statusCode}).`)

      if (finishAction) {
        log.info('Finished handling call operation failed notice.')

        const error = new BasicError({
          message: `Operation ${localOp.operation} failed.`,
          code: callCodes.GENERIC_ERROR
        })

        context.dispatch(
          finishAction(currentCall.id, {
            error,
            transition
          })
        )

        // Notify the app that there has been a call state change
        emitEvent(eventTypes.CALL_STATE_CHANGE, {
          callId: currentCall.id,
          previous: {
            state: currentCall.state,
            localHold: false,
            remoteHold: false
          }
        })

        return
      }
    }

    context.dispatch(callActions.updateCall(currentCall.id))

    emitEvent(eventTypes.CALL_STATE_CHANGE, {
      callId: currentCall.id,
      previous: {
        state: currentCall.state,
        localHold: false,
        remoteHold: false
      }
    })
  }

  return callStatusUpdateFailed
}
