// Other plugins
import { SUBSCRIBE_FINISHED } from '../../../subscription/interface/actionTypes'
import { CONNECT_FINISHED } from '../../../auth/interface/actionTypes'

// src/call/sagaLink/sagas/index.js
export default function createMiddleware (context) {
  const operations = context.container.CallOperations

  function setTurnCredentials (action) {
    if (!shouldHandlePattern(action)) {
      // Don't handle it.
      return
    }

    // Call the set turn credentials operation and forward the action to the operation
    operations.setTurnCredentials(action)
  }

  return setTurnCredentials
}

// The `incomingCallPattern` function.
export function shouldHandlePattern (action) {
  return !!(
    !action.error &&
    ((action.type === CONNECT_FINISHED && action.payload.subscription && action.payload.subscription.turnCredentials) ||
      (action.type === SUBSCRIBE_FINISHED &&
        action.payload.subscriptions &&
        action.payload.subscriptions.length &&
        action.payload.subscriptions[0].turnCredentials))
  )
}
