// Operations.
import remoteStart from './start'
import remoteStop from './stop'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation (bottle) {
  // Remote-only "start MOH" operation.
  bottle.factory('Callstack.operations.startMOH', () => {
    return {
      // Operation handlers.
      remoteOffer: remoteStart(bottle.container),
      // Operation meta-data.
      isNegotiation: true,
      type: OPERATIONS.START_MOH
    }
  })

  // Remote-only "stop MOH" operation.
  bottle.factory('Callstack.operations.stopMOH', () => {
    return {
      // Operation handlers.
      remoteOffer: remoteStop(bottle.container),
      // Operation meta-data.
      isNegotiation: true,
      type: OPERATIONS.STOP_MOH
    }
  })
}
