// Libraries.
import { defaults } from 'lodash/fp'

import { getNotificationConfig } from '../../../interface/selectors'
// Other plugins.
import { getConnectionInfo } from '../../../../auth/interface/selectors'

/**
 * Link-specific push notification factory function.
 * @method createOperation
 * @param  {Object} container The bottle container.
 * @return {Function} The unregisterPushDeviceToken operation.
 */
export default function createOperation (container) {
  const { context, logManager, NotificationRequests } = container
  const log = logManager.getLogger('NOTIFICATIONS')

  /**
   * Operation for deregistering a device token for push notifications.
   * @method deregisterPushDeviceToken
   */
  async function unregisterPushDeviceToken (registration) {
    const connection = getConnectionInfo(context.getState())
    const { pushRegistration } = getNotificationConfig(context.getState())

    // If a push registration endpoint was configured, use that instead of default.
    if (pushRegistration) {
      connection.server = defaults(connection.server, pushRegistration)
      connection.protocol = defaults(connection.protocol, pushRegistration)
      connection.port = defaults(connection.port, pushRegistration)
      connection.version = defaults(connection.version, pushRegistration)
    }

    log.info('Un-registering device token for PUSH notifications...')
    return await NotificationRequests.pushNotificationsDeRegistration(connection, {
      registration
    })
  }

  return unregisterPushDeviceToken
}
