// Call plugin.
import { callActions } from '../../../interface/actions'
import * as actionTypes from '../../../interface/actionTypes'
import * as eventTypes from '../../../interface/eventTypes'
import { normalizeSipUri } from '../../../utils/normalization'
import { OPERATIONS, OP_STATUS, OP_TRANSITIONS } from '../../../interface/constants'

// Other plugins.
import BasicError, { callCodes } from '../../../../errors'
import * as authActions from '../../../../auth/interface/actions'

export default function createAnonOperations (container) {
  const {
    Callstack,
    CallOperations,
    context,
    emitEvent,
    logManager,
    Notifications,
    AuthenticationOperations
  } = container
  const log = logManager.getLogger('CALL')

  async function makeAnonymousCall (callId, participants, credentials, mediaConstraints, options = {}) {
    // An identity for the anonymous user must be provided.
    if (!participants.from) {
      const error = new BasicError({
        code: callCodes.INVALID_PARAM,
        message: 'callOptions has no `from` property'
      })
      context.dispatch(callActions.makeAnonymousCallFinish(callId, { error }))
      emitEvent(eventTypes.CALL_STARTED, {
        callId,
        error
      })

      throw error
    }

    /*
     * If none of these properties exist, then it is a tokenless anonymous call.
     * If all of these properties exist, then it is a time-based token anon call.
     * Any other combination of exist/not-exist is an error scenario.
     */
    const { realm, accountToken, toToken, fromToken } = credentials

    if (
      // If any of them exist, but any other does not exist, then error.
      (realm || accountToken || toToken || fromToken) &&
      (!realm || !accountToken || !toToken || !fromToken)
    ) {
      const error = new BasicError({
        code: callCodes.INVALID_PARAM,
        message:
          'Time-based token anonymous call missing one or more of the following properties: `realm`, `accountToken`, `toToken`, `fromToken`.'
      })
      context.dispatch(callActions.makeAnonymousCallFinish(callId, { error }))
      emitEvent(eventTypes.CALL_STARTED, {
        callId,
        error
      })

      throw error
    }

    let account, anonParticipants
    let isTokenBased
    if (realm && accountToken && toToken && fromToken) {
      // If credentials were provided for a token-limited call.
      log.debug('Performing time-limited token anonymous call.')
      account = accountToken
      anonParticipants = { to: toToken, from: fromToken }
      isTokenBased = true
    } else {
      log.debug('Performing anonymous call.')
      account = participants.to
      anonParticipants = {
        to: normalizeSipUri(participants.to),
        from: normalizeSipUri(participants.from)
      }
      isTokenBased = false
    }

    try {
      // Dispatch a CONNECT action for backwards compatability
      context.dispatch(
        authActions.connect({
          username: account,
          password: '',
          // The fake userId of the current, anonymous user.
          anonUsername: participants.from,
          realm
        })
      )

      await AuthenticationOperations.anonymousConnect({
        username: account,
        password: '',
        // The fake userId of the current, anonymous user.
        anonUsername: participants.from,
        realm
      })
    } catch (err) {
      const error = new BasicError({
        code: callCodes.INVALID_PARAM,
        message: 'Failed to connect to server.'
      })
      context.dispatch(callActions.makeAnonymousCallFinish(callId, { error }))
      emitEvent(eventTypes.CALL_STARTED, {
        callId,
        error
      })

      throw error
    }

    let error
    try {
      const anonOptions = {
        ...options,
        isAnonymous: true,
        account,
        displayName: isTokenBased ? '' : options.displayName
      }

      emitEvent(eventTypes.CALL_OPERATION, {
        callId,
        isLocal: true,
        operation: OPERATIONS.MAKE,
        transition: OP_TRANSITIONS.START,
        previous: undefined
      })

      emitEvent(eventTypes.CALL_STARTED, { callId })

      // Use the regular "make call" operation with the anonymous parameters.
      await Callstack.operations.make(callId, anonParticipants, mediaConstraints, anonOptions)

      // When the call ends, clean-up the anon subscription.
      Notifications.takeAction(actionTypes.END_CALL_FINISH).then(action => {
        CallOperations.anonymousCallEnd()
      })
    } catch (err) {
      error = err
      // If the call fails to be made, then clean-up the anon subscription.
      CallOperations.anonymousCallEnd()
    }

    emitEvent(eventTypes.CALL_OPERATION, {
      callId,
      isLocal: true,
      operation: OPERATIONS.MAKE,
      // If the operation failed, then it is finished, but otherwise we are
      //    waiting on the remote update for the operation to complete.
      transition: error ? OP_TRANSITIONS.FINISH : OP_TRANSITIONS.UPDATE,
      previous: {
        operation: OPERATIONS.MAKE,
        status: OP_STATUS.ONGOING
      },
      error: error
    })
  }

  return makeAnonymousCall
}
