import { REPORT_EVENTS, REPORT_METRICS } from './constants'

/**
 * @method durationHandler
 * @param {string} metric The metric type which will be added to report when handler function is executed.
 * @param {Array} startEvents The set of event types whose `start` timestamp will be used to calculate the metric value.
 * @param {boolean} autoUnregister Specifies wether to automatically unregister the event handler function after it executed once.
 *   By default (if not specified), it will unregister the handler once it executed.
 * @returns {undefined}
 */
function durationHandler (metric, startEvents, autoUnregister = true) {
  /**
   * Handler function which executes when a certain registered event has ended.
   * @return {boolean} Returns true if handler was executed AND metric was saved. False otherwise.
   */
  return function (callReport, event) {
    // First search for the event in the timeline of the report (i.e. see if it's a top-level event).
    // Loop through the timeline backwards to find the last type of the specified start event in the case of
    // mutiple identical operations (i.e., multiple holds).
    let startEvent
    for (let i = callReport.timeline.length - 1; i >= 0; i--) {
      const timelineEvent = callReport.timeline[i]
      if (startEvents.includes(timelineEvent.type)) {
        startEvent = timelineEvent
        break
      } else {
        // Check the sub-events for the start event (e.g., set local description is a common start event)
        for (let j = 0; j <= timelineEvent.timeline.length - 1; j++) {
          const timelineSubEvent = timelineEvent.timeline[j]
          if (startEvents.includes(timelineSubEvent.type)) {
            startEvent = timelineSubEvent
            break
          }
        }
        // If we found the start event in the sub-events break out of the loop
        if (startEvent) {
          break
        }
      }
    }

    if (startEvent) {
      // PROCESS_RESPONSE event is triggered as part of many SDK operations and on both sides of the call.
      // But we don't want to add a 'MAKE_CALL_REMOTE_SETUP' metric for the callee side,
      // because it only makes sense for caller side.
      if (
        event.type === REPORT_EVENTS.PROCESS_RESPONSE &&
        metric === 'MAKE_CALL_REMOTE_SETUP' &&
        event.getData('operation') !== 'MAKE'
      ) {
        return false
      }

      // Special-case: For ice collection time, add the operation that triggered the ice collection
      if (metric === REPORT_METRICS.TIME_TO_COLLECT_ICE_CANDIDATES) {
        callReport.addMetric(metric, {
          operation: startEvent.getData('operation'), // Get the operation from the set local desc event data
          duration: event.end - startEvent.start
        })
      } else {
        // We found the start event, so save its associated metric containing the measurement.
        callReport.addMetric(metric, event.end - startEvent.start)
        if (autoUnregister) {
          // We automatically unregister the handler once it was executed.
          callReport.unregisterMetricHandler(metric)
        }
      }
      return true
    }
    return false
  }
}

/**
 * @method callPreSDPHandler
 * @param {string} metric The metric type which will be added to report when handler function is executed.
 * @param {Array} startEvents The set of event types whose `start` timestamp will be used to calculate the metric value.
 * @returns {undefined}
 */
function callPreSDPHandler (metric, startEvents) {
  /**
   * Handler function which executes when a certain registered event has ended.
   * @return {boolean} Returns true if handler was executed AND metric was saved. False otherwise.
   */
  return function (callReport, event) {
    // Search for the event in the timeline of the report
    const startEvent = callReport.timeline.find(event => startEvents.includes(event.type))

    if (startEvent) {
      callReport.addMetric(metric, event.start - startEvent.start)
      callReport.unregisterMetricHandler(metric)
      return true
    }
    return false
  }
}

/**
 * @method joinedCallDurationHandler
 * @param {string} metric The metric type which will be added to report when handler function is executed.
 * @param {Array} startEvents The set of event types whose `start` timestamp will be used to calculate the metric value.
 * @returns {undefined}
 */
function joinedCallDurationHandler (metric, startEvents) {
  /**
   * Handler function which executes when a certain registered event has ended.
   * @return {boolean} Returns true if handler was executed AND metric was saved. False otherwise.
   */
  return function (callReport, event) {
    // Search for the event in the timeline of the report
    const startEvent = callReport.timeline.find(event => startEvents.includes(event.type))

    // startEvent !== event is for the special case join operation which is used as the end for the initial
    // two calls, but the starting event for the joined call.
    if (startEvent && startEvent !== event) {
      // We found the start event, so save its associated metric containing the measurement.
      callReport.addMetric(metric, event.end - startEvent.start)
      // We automatically unregister the handler once it was executed.
      callReport.unregisterMetricHandler(metric)
      return true
    }
    return false
  }
}

/**
 * A function that gathers the relay candidates as they are collected and then returns a handler function that will
 * create the metric for the relay candidates after collection is complete.
 *
 * Each time a relay candidate collected event is received it will create an object with that relay candidates data and
 * store it in an array. When the set local description event ends it will update all the collected candidates in the array
 * with the correct duration, and add a metric to the call report for them.
 * @method relayCandidatesHandler
 * @returns {Function} The metric handler function
 */
function relayCandidatesHandler () {
  const collectedCandidates = []
  return (callReport, event) => {
    if (event.type === REPORT_EVENTS.RELAY_CANDIDATE_COLLECTED) {
      const eventData = event.getData()
      const collectedCandidateInformation = {
        ...eventData,
        duration: event.start // The candidate was collected by the time this event was added
      }
      collectedCandidates.push(collectedCandidateInformation)
    } else if (event.type === REPORT_EVENTS.SET_LOCAL_DESCRIPTION) {
      // Update the duration of all the collected candidates
      collectedCandidates.map(candidateInfo => {
        candidateInfo.duration = candidateInfo.duration - event.start
        return collectedCandidates
      })
      // Only need to add metric if there will be any data...
      if (collectedCandidates.length) {
        callReport.addMetric(REPORT_METRICS.TIME_TO_RELAY_CANDIDATES, {
          operation: event.getData('operation'),
          candidates: [...collectedCandidates]
        })
      }
      collectedCandidates.length = 0
    }
  }
}

// Generic function to register all metric handlers to the call reporter
function registerAllMetricHandlers (callReport) {
  // Register the call duration handler
  callReport.registerMetricHandler(
    REPORT_METRICS.CALL_DURATION,
    [
      REPORT_EVENTS.END_LOCAL,
      REPORT_EVENTS.END_REMOTE,
      REPORT_EVENTS.JOIN,
      REPORT_EVENTS.DIRECT_TRANSFER,
      REPORT_EVENTS.CONSULTATIVE_TRANSFER
    ],
    joinedCallDurationHandler(REPORT_METRICS.CALL_DURATION, [
      REPORT_EVENTS.MAKE,
      REPORT_EVENTS.RECEIVE_CALL,
      REPORT_EVENTS.JOIN
    ])
  )

  // Register the local-setup handler
  callReport.registerMetricHandler(
    REPORT_METRICS.MAKE_CALL_LOCAL_SETUP,
    [REPORT_EVENTS.SET_LOCAL_DESCRIPTION],
    durationHandler(REPORT_METRICS.MAKE_CALL_LOCAL_SETUP, [REPORT_EVENTS.MAKE])
  )

  // Register the handler for collecting ice candidates during the local-setup.
  // The start event will be SET_LOCAL_DESCRIPTION and the metric will be saved when
  // its sub-event (i.e. ICE_COLLECTION) has ended.
  // Since SET_LOCAL_DESCRIPTION event occurs on both caller & callee, its sub-event: ICE_COLLECTION
  // will also be available for each party.
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_COLLECT_ICE_CANDIDATES,
    [REPORT_EVENTS.ICE_COLLECTION],
    durationHandler(REPORT_METRICS.TIME_TO_COLLECT_ICE_CANDIDATES, [REPORT_EVENTS.SET_LOCAL_DESCRIPTION], false)
  )

  // Register the remote-setup handler
  callReport.registerMetricHandler(
    REPORT_METRICS.MAKE_CALL_REMOTE_SETUP,
    [REPORT_EVENTS.PROCESS_RESPONSE],
    durationHandler(REPORT_METRICS.MAKE_CALL_REMOTE_SETUP, [REPORT_EVENTS.REST_REQUEST])
  )

  // Register the time-to-ringing handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_RINGING,
    [REPORT_EVENTS.REMOTE_RINGING],
    durationHandler(REPORT_METRICS.TIME_TO_RINGING, [REPORT_EVENTS.MAKE])
  )

  /*
   * TIME_FROM_RECEIVE_TO_ANSWER
   * Metric from when an incoming call is received until it has been answered.
   * Duration includes:
   *    - time for processing call notification,
   *    - time for application to call the `answer` API, and
   *    - time for `answer` operation to complete.
   * This metric is for the special-case of incoming calls being auto-answered.
   * This is the sum of TIME_TO_CALL_SETUP_DURATION and the answer REST request duration.
   */
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_FROM_RECEIVE_TO_ANSWER,
    REPORT_EVENTS.ANSWER,
    durationHandler(REPORT_METRICS.TIME_FROM_RECEIVE_TO_ANSWER, [REPORT_EVENTS.RECEIVE_CALL])
  )

  // Time to setup incoming call (incoming call notification until media is connected)
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_CALL_SETUP_DURATION,
    REPORT_EVENTS.SET_LOCAL_DESCRIPTION,
    durationHandler(REPORT_METRICS.TIME_TO_CALL_SETUP_DURATION, [REPORT_EVENTS.RECEIVE_CALL])
  )

  // Time to setup incoming call (answer call operation until media is connected)
  callReport.registerMetricHandler(
    REPORT_METRICS.ANSWER_CALL_LOCAL_SETUP,
    REPORT_EVENTS.SET_LOCAL_DESCRIPTION,
    durationHandler(REPORT_METRICS.ANSWER_CALL_LOCAL_SETUP, [REPORT_EVENTS.ANSWER])
  )

  // Register the time-to-relay-candidates handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_RELAY_CANDIDATES,
    [REPORT_EVENTS.SET_LOCAL_DESCRIPTION, REPORT_EVENTS.RELAY_CANDIDATE_COLLECTED],
    relayCandidatesHandler()
  )

  // Register the answer-call-pre-local-setup handler
  callReport.registerMetricHandler(
    REPORT_METRICS.ANSWER_CALL_PRE_LOCAL_SETUP,
    REPORT_EVENTS.SET_LOCAL_DESCRIPTION,
    callPreSDPHandler(REPORT_METRICS.ANSWER_CALL_PRE_LOCAL_SETUP, [REPORT_EVENTS.ANSWER])
  )

  // Register the make-call-pre-local-setup handler
  callReport.registerMetricHandler(
    REPORT_METRICS.MAKE_CALL_PRE_LOCAL_SETUP,
    REPORT_EVENTS.SET_LOCAL_DESCRIPTION,
    callPreSDPHandler(REPORT_METRICS.MAKE_CALL_PRE_LOCAL_SETUP, [REPORT_EVENTS.MAKE])
  )

  /*
   * **************************
   * TIME-TO-OPERATION metrics.
   *  ie. metrics that are the duration of a single event.
   * **************************
   */

  // Time to reject incoming call
  callReport.registerMetricHandler(REPORT_METRICS.TIME_TO_REJECT, [REPORT_EVENTS.REJECT], (report, event) => {
    callReport.addMetric(REPORT_METRICS.TIME_TO_REJECT, event.end - event.start)
  })

  // Register the time-to-ignore handler
  callReport.registerMetricHandler(REPORT_METRICS.TIME_TO_IGNORE, [REPORT_EVENTS.IGNORE], (report, event) => {
    callReport.addMetric(REPORT_METRICS.TIME_TO_IGNORE, event.end - event.start)
  })

  // Register the time-to-add-media handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_ADD_MEDIA,
    [REPORT_EVENTS.ADD_MEDIA, REPORT_EVENTS.ADD_BASIC_MEDIA],
    (report, event) => {
      callReport.addMetric(REPORT_METRICS.TIME_TO_ADD_MEDIA, event.end - event.start)
    }
  )

  // Register the time-to-add-media-remote handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_ADD_MEDIA_REMOTE,
    [REPORT_EVENTS.ADD_MEDIA_REMOTE],
    (report, event) => {
      callReport.addMetric(REPORT_METRICS.TIME_TO_ADD_MEDIA_REMOTE, event.end - event.start)
    }
  )

  // Register the time-to-hold-local handler
  callReport.registerMetricHandler(REPORT_METRICS.TIME_TO_HOLD_LOCAL, [REPORT_EVENTS.HOLD_LOCAL], (report, event) => {
    callReport.addMetric(REPORT_METRICS.TIME_TO_HOLD_LOCAL, event.end - event.start)
  })

  // Register the time-to-hold-remote handler
  callReport.registerMetricHandler(REPORT_METRICS.TIME_TO_HOLD_REMOTE, [REPORT_EVENTS.HOLD_REMOTE], (report, event) => {
    callReport.addMetric(REPORT_METRICS.TIME_TO_HOLD_REMOTE, event.end - event.start)
  })

  // Register the time-to-unhold-local handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_UNHOLD_LOCAL,
    [REPORT_EVENTS.UNHOLD_LOCAL],
    (report, event) => {
      callReport.addMetric(REPORT_METRICS.TIME_TO_UNHOLD_LOCAL, event.end - event.start)
    }
  )

  // Register the time-to-unhold-remote handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_UNHOLD_REMOTE,
    [REPORT_EVENTS.UNHOLD_REMOTE],
    (report, event) => {
      callReport.addMetric(REPORT_METRICS.TIME_TO_UNHOLD_REMOTE, event.end - event.start)
    }
  )

  // Register the time-to-remove-media handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_REMOVE_MEDIA,
    [REPORT_EVENTS.REMOVE_MEDIA, REPORT_EVENTS.REMOVE_BASIC_MEDIA],
    (report, event) => {
      callReport.addMetric(REPORT_METRICS.TIME_TO_REMOVE_MEDIA, event.end - event.start)
    }
  )

  // Register the time-to-remove-media-remote handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_REMOVE_MEDIA_REMOTE,
    [REPORT_EVENTS.REMOVE_MEDIA_REMOTE],
    (report, event) => {
      callReport.addMetric(REPORT_METRICS.TIME_TO_REMOVE_MEDIA_REMOTE, event.end - event.start)
    }
  )

  // Register the time-to-restart-media handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_RESTART_MEDIA,
    REPORT_EVENTS.MEDIA_RESTART,
    (report, event) => {
      callReport.addMetric(REPORT_METRICS.TIME_TO_RESTART_MEDIA, event.end - event.start)
    }
  )

  // Register the time-to-send-custom-paramters handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_SEND_CUSTOM_PARAMETERS,
    REPORT_EVENTS.SEND_CUSTOM_PARAMETERS,
    (report, event) => {
      callReport.addMetric(REPORT_METRICS.TIME_TO_SEND_CUSTOM_PARAMETERS, event.end - event.start)
    }
  )

  // Time to forward incoming call
  callReport.registerMetricHandler(REPORT_METRICS.TIME_TO_FORWARD, [REPORT_EVENTS.FORWARD_CALL], (report, event) => {
    callReport.addMetric(REPORT_METRICS.TIME_TO_FORWARD, event.end - event.start)
  })

  // Register the time-to-direct-transfer handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_DIRECT_TRANSFER,
    [REPORT_EVENTS.DIRECT_TRANSFER],
    (report, event) => {
      callReport.addMetric(REPORT_METRICS.TIME_TO_DIRECT_TRANSFER, event.end - event.start)
    }
  )

  // Register the time-to-consultative-transfer handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_CONSULTATIVE_TRANSFER,
    [REPORT_EVENTS.CONSULTATIVE_TRANSFER],
    (report, event) => {
      callReport.addMetric(REPORT_METRICS.TIME_TO_CONSULTATIVE_TRANSFER, event.end - event.start)
    }
  )

  // Register the time-to-join handler
  callReport.registerMetricHandler(
    REPORT_METRICS.TIME_TO_JOIN,
    REPORT_EVENTS.JOIN,
    joinedCallDurationHandler(REPORT_METRICS.TIME_TO_JOIN, [REPORT_EVENTS.JOIN])
  )

  // Register the time-to-make-operation handler
  callReport.registerMetricHandler(REPORT_METRICS.TIME_TO_MAKE, REPORT_EVENTS.MAKE, (report, event) => {
    callReport.addMetric(REPORT_METRICS.TIME_TO_MAKE, event.end - event.start)
  })

  // Register the time-to-answer-operation handler
  callReport.registerMetricHandler(REPORT_METRICS.TIME_TO_ANSWER, REPORT_EVENTS.ANSWER, (report, event) => {
    callReport.addMetric(REPORT_METRICS.TIME_TO_ANSWER, event.end - event.start)
  })
}

export default {
  handlers: {
    durationHandler,
    joinedCallDurationHandler,
    relayCandidatesHandler
  },
  registerAllMetricHandlers
}
