// Webrtc plugin.
import pluginInterface from './interface'
import createAPI from './interface/api/index'
import createMediaOperations from './operations/media'
import createLogOperations from './operations/logs'
import { miscActions } from './interface/actions'
import watchManagers from './channels'
import createMiddleware from './middleware'

// Other plugins.
import { logManager } from '../logs'
import { getLevel } from '../logs/interface/selectors'

// Libraries.
import initialize, { getBrowserDetails, getWebRTCSupportCapabilities } from 'kandy-webrtc'

const log = logManager.getLogger('WEBRTC')

/**
 * WebRTC plugin factory.
 * @method webrtcPlugin
 */
export default function webrtcPlugin (options = {}, bottle) {
  const { mediaDevices, peerConnection } = getWebRTCSupportCapabilities()
  if (!mediaDevices || !peerConnection) {
    log.warn(
      'Calls are not supported on this platform due to lack of WebRTC support. Media APIs will not be available.'
    )
    return
  }

  // Initialize the webRTC.
  const webRTC = initialize()

  // Register the component factory functions to the bottle.
  bottle.factory('WebRTCAPI', createAPI)
  bottle.factory('WebRTCMediaOperations', createMediaOperations)
  bottle.factory('WebRTCLogOperations', createLogOperations)

  // Register the WebRTC stack to the bottle. This lets other component access it.
  bottle.factory('WebRTC', () => webRTC.managers)

  // Register a function to check browser info after SDK initialization has finished.
  bottle.defer(initWebrtc)

  // Configure the WebRTC stack's log level on SDK initialization.
  bottle.defer(initLogLevel)

  // Register a function to start listening for events from the WebRTC managers after SDK initialization has finished.
  bottle.defer(watchManagers)

  return {
    name: 'webrtc',
    reducer: pluginInterface.reducer,
    middleware: createMiddleware
  }
}

/**
 * WebRTC initialization function to check and set browser details.
 * @method initWebrtc
 * @param {Object} container The bottle container.
 */
export function initWebrtc (container) {
  const { logManager, context } = container
  const log = logManager.getLogger('WEBRTC')

  // Use the webRTC stack to check the browser details.
  const details = getBrowserDetails()
  log.info(`Browser details: ${details.browser}, version ${details.version}.`)
  context.dispatch(miscActions.setBrowserDetails(details))
}

/**
 * WebRTC initialization function to set the log level on SDK initialization
 * @method initLogLevel
 * @param  {Object} container The bottle container.
 */
export function * initLogLevel (container) {
  const { WebRTCLogOperations: operations } = container
  // Get the WEBRTC logger's level.
  const level = getLevel('WEBRTC')
  // Call the "set log level" saga for the WEBRTC logger, pretending that we
  //    received an action updating its level.
  operations.updateLogLevel({ WEBRTC: level })
}
