// Auth plugin.
import * as actions from '../../interface/actions'
import * as eventTypes from '../../interface/eventTypes'
import { getSubscriptionInfo, getConnectionInfo } from '../../interface/selectors'

// Other plugins
import { getConnectionState } from '../../../connectivity/interface/selectors'
import { wsDisconnect } from '../../../connectivity/interface/actions'
import { WS_DISCONNECT_FINISHED, WS_ERROR } from '../../../connectivity/interface/actionTypes'

// Constants
import { platforms } from '../../../constants'

export default function createOperation (container) {
  const { logManager, context, emitEvent, Notifications, AuthenticationRequests, AuthenticationIntervals } = container
  const log = logManager.getLogger('AUTH')

  /**
   * Link disconnect function.
   * Performs the actions for actually disconnecting from SPiDR.
   * @method disconnect
   */
  async function anonymousDisconnect () {
    log.info('Unsubscribing from callMe services.')

    // Emit an auth changed event
    emitEvent(eventTypes.AUTH_CHANGE, {})

    // Stop the resubscribe interval
    AuthenticationIntervals.resubInterval.stopResubInterval()

    // Retrieve needed info from the store.
    const subscription = getSubscriptionInfo(context.getState())
    const connection = getConnectionInfo(context.getState())

    // If the above info is not present, we probably got a disconnect mid-connection attempt.
    if (subscription && connection) {
      // If the info is in the store we can go ahead and unsubscribe.
      try {
        await AuthenticationRequests.unsubscribe(connection, subscription.url)
      } catch (error) {}
    }

    log.info('Successfully unsubscribed from services.')
    const wsState = getConnectionState(context.getState(), platforms.LINK)
    if (wsState.connected) {
      log.info('Disconnecting from websocket.')
      // disconnect from the websocket
      context.dispatch(wsDisconnect(undefined, platforms.LINK))

      // Wait for the action that signifies the result of the above action.
      await Notifications.takeAction(action => {
        return (
          (action.type === WS_DISCONNECT_FINISHED || action.type === WS_ERROR) &&
          action.meta.platform === platforms.LINK
        )
      })

      log.info('Successfully disconnected from websocket.')
    }

    // Dispatch disconnect finished action appropriate for the response.
    context.dispatch(actions.disconnectFinished())

    // Emit an auth changed event
    emitEvent(eventTypes.AUTH_CHANGE, {})
  }

  return anonymousDisconnect
}
