// Operations.
import localOffer from './make'
import remoteOffer from './incomingCall'

import remoteAnswer from './remoteAnswer'

// Helpers
import setupIncomingCall from './setupIncomingCall'
import setupOutgoingSession from './setupOutgoingSession'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation (bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.make', () => {
    const operation = localOffer(bottle.container)
    operation.remoteAnswer = remoteAnswer(bottle.container)
    // Add meta-data to the operation for the CallManager.
    operation.isNegotiation = true
    operation.type = OPERATIONS.MAKE
    return operation
  })
  bottle.factory('Callstack.notifications.incomingCall', () => remoteOffer(bottle.container))

  bottle.factory('Callstack.utils.setupIncomingCall', () => setupIncomingCall(bottle.container))
  bottle.factory('Callstack.utils.setupOutgoingSession', () => setupOutgoingSession(bottle.container))
}
