import makeAnonymous from './makeAnonymous'
import { OPERATIONS } from '../../../interface/constants'

import remoteAnswer from '../../../callstack/operations/make/remoteAnswer'

/*
 * Register the operation with the bottle. This will make it available on the
 *    top-level container under its namespace.
 */
export default function registerOperation (bottle) {
  bottle.factory('Callstack.operations.makeAnonymousCall', () => {
    const operation = makeAnonymous(bottle.container)
    operation.remoteAnswer = remoteAnswer(bottle.container)
    operation.isNegotiation = true
    operation.type = OPERATIONS.MAKE_ANONYMOUS
    return operation
  })
}
