// Call plugin
import { getCallByWrtcsSessionId } from '../../interface/selectors'
import { getRemoteParticipant } from '../utils'

// Other plugins
import { NOTIFICATION_RECEIVED } from '../../../notifications/interface/actionTypes'
import { getDomain } from '../../../auth/interface/selectors'

export default function createMiddleware (context) {
  const { CallManager, logManager } = context.container

  async function callStatusUpdateFailedNotification (action) {
    if (!shouldHandlePattern(action)) {
      // Don't handle it.
      return
    }

    const message = action.payload.notificationMessage
    const { sessionData: wrtcsSessionId, reasonText, statusCode } = message.sessionParams

    const targetCall = getCallByWrtcsSessionId(context.getState(), wrtcsSessionId)

    const log = logManager.getLogger('CALL', targetCall ? targetCall.id : undefined)
    if (!targetCall) {
      log.info('A remote operation is being requested on a call that no longer exists.')
    }

    const domain = getDomain(context.getState())
    const remoteInfo = getRemoteParticipant(targetCall, message, domain)

    const params = {
      reasonText,
      statusCode,
      // Remote participant's information.
      ...remoteInfo
    }

    await CallManager.callStatusUpdateFailed(wrtcsSessionId, params)
  }

  return callStatusUpdateFailedNotification
}

export function shouldHandlePattern (action) {
  return action.type === NOTIFICATION_RECEIVED && action.payload.notificationMessage.eventType === 'sessionFail'
}
