import subscribeRequest from './subscribe'
import resubscribeRequest from './resubscribe'

/**
 * Register the requests with the bottle. This will make it available on the
 * top-level container under its namespace.
 */
export default function registerRequests (bottle) {
  bottle.factory('AuthenticationRequests.subscribe', () => {
    return subscribeRequest(bottle.container)
  })
  bottle.factory('AuthenticationRequests.resubscribe', () => {
    return resubscribeRequest(bottle.container)
  })
}
