import complexRequests from './complex'
import establishRequests from './establish'
import miscRequests from './misc'
import negotiationRequests from './negotiation'

/**
 * Link-specific Call Requests factory function.
 * Creates a closure where dependencies can be set and used
 *    by requests.
 */
export default function createRequests (container) {
  return {
    ...complexRequests(container),
    ...establishRequests(container),
    ...miscRequests(container),
    ...negotiationRequests(container)
  }
}
