// Constants
import { platforms } from '../../../constants'

// Auth plugin
import * as actions from '../../interface/actions'
import { getSubscriptionInfo, getConnectionInfo, getAuthScenario } from '../../interface/selectors'
import { AUTH_RESUB } from '../../interface/eventTypes'

/**
 * Authentication resubscription interval factory function.
 * @method createInterval
 * @param {Object} container The bottle container.
 * @return {Object} Available interval for authentication resubscription.
 */
export default function createInterval (container) {
  const { context, createInterval, AuthenticationRequests, AuthenticationOperations, emitEvent } = container

  // Interval function for resubscription
  let resubInterval

  // Number of retry attemps
  let attemptNum = 1

  /**
   * Check if the interval is running.
   * @method isRunning
   * @return {boolean} The interval is running or not.
   */
  function isRunning () {
    return !!resubInterval
  }

  /**
   * Start the interval.
   * @method start
   */
  function start (update, expires) {
    // Set the delay to be halfway between now and when the subscription expires.
    let resubDelay = (expires * 1000) / Math.pow(2, attemptNum)
    // Don't try to resub more often than every 5 minutes.
    resubDelay = resubDelay > 30000 ? resubDelay : 30000

    resubInterval = createInterval(update, resubDelay)

    resubInterval.start()
  }

  /**
   * Start a new interval timer for resubscribing to services.
   * @method startResubInterval
   * @param {Object} The connection to use for resubscribing to services.
   * @throws {Error} If the interval is already running.
   */
  async function startResubInterval (connection) {
    if (isRunning()) {
      throw new Error('There is already an interval running for this subscription.')
    }

    // Setup connection and subscription
    const isOnSSO = getAuthScenario(context.getState())
    let subscription = {}
    let conn = {}
    if (isOnSSO) {
      const linkConnection = getConnectionInfo(context.getState())
      conn = {
        server: linkConnection.server,
        token: connection.token
      }
      subscription = getSubscriptionInfo(context.getState())
    } else {
      conn = getConnectionInfo(context.getState())
      subscription = getSubscriptionInfo(context.getState())
    }

    // Update our subscription
    async function update () {
      try {
        await AuthenticationRequests.resubscribe(conn, subscription)
        context.dispatch(actions.resubscribeFinished({ attemptNum }, platforms.LINK))

        emitEvent(AUTH_RESUB, {
          attemptNum,
          isFailure: false
        })
      } catch (error) {
        // Check the error a specific status code that indicates "resource does not exist" and disconnect the websocket
        if (error && error.payload && error.payload.code === 39) {
          // Dispatch a resubscribe finished action for backwards compatability
          context.dispatch(actions.resubscribeFinished({ error, attemptNum }, platforms.LINK))

          // Emit an `auth:resub` event indicating a failure
          emitEvent(AUTH_RESUB, {
            attemptNum,
            isFailure: true
          })

          // Disconnect the websocket when our subscription is gone.
          await AuthenticationOperations.onSubscriptionGone()

          // Stop the resub interval since we aren't going to get the subscription
          resubInterval.stop()
        } else {
          context.dispatch(
            actions.resubscribeFinished(
              {
                error,
                attemptNum
              },
              platforms.LINK
            )
          )

          emitEvent(AUTH_RESUB, {
            attemptNum,
            isFailure: true
          })

          // Increment our attempts
          attemptNum++
        }
      }
    }

    // Start the interval
    start(update, subscription.expires)
  }

  /**
   * Stop the resubscription interval.
   * @method stopResubInterval
   */
  function stopResubInterval () {
    if (resubInterval) {
      resubInterval.stop()
      resubInterval = undefined
    }
  }

  return {
    startResubInterval,
    stopResubInterval
  }
}
