// Constants
import { platforms } from '../../../constants'

// Auth plugin
import * as actions from '../../interface/actions'
import * as eventTypes from '../../interface/eventTypes'
import { DISCONNECT_REASONS } from '../../constants'

// Other plugins
import { getConnectionState } from '../../../connectivity/interface/selectors'
import { disconnectWebsocket } from '../../../connectivity/interface/effects'

/**
 * Operation factory function responsible for handling the 'subscription gone' notification.
 * @method createOperation
 * @param  {Object} container The bottle container.
 * @return {Function} The 'handling of loss of subscription' operation.
 */
export default function createOperation (container) {
  const { context, emitEvent } = container

  async function onSubscriptionGone () {
    // Dispatch an action to disconnect the websocket (and let the connectivity
    //      plugin know we expect it to be disconnected).
    const wsState = getConnectionState(context.getState(), platforms.LINK)
    if (wsState.connected) {
      disconnectWebsocket(undefined, platforms.LINK)
    }

    // Dispatch a disconnect finished action to trigger "user disconnected" logic.
    context.dispatch(actions.disconnectFinished({ reason: DISCONNECT_REASONS.GONE }))

    // Dispatch an event with same payload, for backwards compatibility.
    emitEvent(eventTypes.AUTH_CHANGE, { reason: DISCONNECT_REASONS.GONE })
  }

  return onSubscriptionGone
}
